:root {
  --grey: grey;
  --yellow: yellow;
  --bgLightGrey: #d3d3d3;
  --bgDarkGrey: #0c0c0c;
  --orange: #f9920b;
  --bgLightOrang: #fde0b9;
}
.App {
  background: white;
}
.App {
  text-align: center;
  min-height: 1000px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: var(--bgDarkGrey);
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 50px;
  margin: 0 0 50px 0;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.btn {
  border-radius: 10px;
  margin: 20px;
  background: var(--orange);
}
.ipfs__section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
}
.dropzone__wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
.dropzone__wrap div {
  background: var(--grey);
  width: 700px !important;
}
.ipfs__btn__send {
  height: 50px;
  width: 300px !important;
}
.btn__disabled {
  background: var(--bgLightOrang);
}
.btn__active {
  background: var(--orange);
}
.ipfs__btn__send {
  width: 100px;
}



